/* ========= Post list ================ */
.post-list{
	display: flex;
	flex-direction: column;
	align-items: center;

	.modify-post {
		@extend %popup;
		@extend %form-container;
		form {
			background-color: $background;
			padding: 30px;
			border-radius: 10px;
			margin: auto;
			width: 50%;
		}
	}

	.post-item{
		@extend %lifted-surface;

		border-radius: 20px;
		background-color: $surface;
		margin: 30px;
		width: 50%;

		.header{
			display: flex;
			flex-direction: row;
			justify-content: space-between;

			padding: 20px;
			text-align: start;
			align-self: flex-start;
			border-bottom: 1px solid gray;

			.header__title {
				display: flex;
				flex-direction: column;
				p { margin: 8px;}
			}
			.header__actions {
				i { margin-left: 20px; cursor: pointer; }
				.header__actions__edit:hover {
					color: $primary;
				}
				.header__actions__delete:hover {
					color: $error;
				}
			}
		}

		.body{
			padding: 20px;
			text-align: start;
		}
	}
	@import './post__comments.scss';
}

.post-form, .assignment-form {
	@extend %form-container;
}
