.student-list{
  display: flex;
  flex-direction: column;
  align-items: center;

  .action-buttons {
    margin: 40px;
    a{
      @extend %action-button;
      width: 10vw;
      min-width: 200px;
      padding: 1rem;
      margin: 1rem;
      border-radius: 4vh;
      text-decoration: none;
    }
  }

  .grades{

    width: 50%;
    margin-bottom: 20px;

    .head{
      width: 100%;
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
      align-items: center;
      color: $onPrimary;
      background-color: $primary;
      h4{flex: 4;}
      i{flex: 1;}
    }

    .body{
      @extend %table;
      width: 100%;
      height: 100%;

      .hidden{
        display: none;
      }

      table {
        tr:hover {
          background-color: $secondary;
          color: $onSecondary;
          td, th{
            background-color: $secondary;
            color: $onSecondary;
          }
        }
        td, th{
          background-color: $surface;
          color: $onSurface;
        }
      }
    }
  }
}

/* The styling for the grades on the student page */
.student-grades {
	@extend %table;
	width: 100%;
	height: 100%;
	margin: auto;
}

.add-grades{
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.update-grade {
  @extend %form-container;
  form {
    width: 50%;
  }
}
