.class {
	@import './course/grades';
	width: 100%;
	.courses-list {
		padding-top: 40px;
		width: 100%;
		@extend %table;
		table {
			.arrow {
				width: 30px;
				i { color: $onBackgroundFaded; }
				i:hover { color: $primary; cursor: pointer; }
			}
			.color {
				width: 30px;
				div {
					width: 30px;
					height: 30px;
					background-color: $primary;
					border-radius: 50%;
				}
			}
			.name {
				color: black;
			}
			.category, .hours, .teacher {color: grey;}
			.grade {color: $primary;}
		}
	}
}
