/* =========== Attendance ===================== */
.add-session, .add-grades{
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	margin-top: 50px;
	label {
		width: 40vw;
		display: flex;
		flex-direction: column;
		margin: 0.8rem;
		color: $onBackground;
		input , select{
			width: 90%;
			border: none;
			background-color: $surface;
			color: $onSurface;
			padding: 0.5rem;
			&:focus {
				outline-color: $secondary;
				border: none;
			}
		}
	}
	button{
		@extend %action-button;
		padding: 1rem;
	}

	table{
		width: 100%;
		border-collapse: collapse;
		th, td{
			border-bottom: 1px solid $onBackground;
			padding: 1rem;
		}
		td{
			background-color: $surface;
		}
	}

}

.horizontal-divider{
  width: 100%;
  margin-top: 10vh;
  margin-bottom: 10vh;
  border-bottom: 3px dotted $primary;
}

.sessions, .session{
  padding-top: 20px;
  @extend %table;
  a{
    @extend %action-button;
    width: 10vw;
    min-width: 200px;
    padding: 2vh;
    border-radius: 4vh;
    text-decoration: none;
  }
}

.session{
  button.delete{
    @extend %danger-button;
    padding: 1rem;
    margin: 1rem;
    border-radius: 4vh;
	}
}
