/* The styling for the courses surface */
.statistics {
	@extend %lifted-surface;
	display: flex;
	flex-direction: column;

	margin-bottom: 50px;

	padding: 10px 10px 30px 10px;

	background-color: $surface;
	strong { color: $onSurface; }

	.statistics__head {
		border-bottom: 1px solid $primary;
		display: flex;
		flex-direction: row;
		color: $primary;
		padding-left: 30px;
	}

	.statistics__numbers {
		padding: 10px;
		display: flex;
		flex-direction: row;
		justify-content: space-around;

		.statistics__numbers__grades-count {
			padding: 0 20px 0 0;
			margin-right: 20px;
			p {font-size: 15px;}
			strong {color: $primary; font-size: 25px;}
			border-right: 1px solid $onSurface;
		}

		.statistics__numbers__min-grade {
			padding: 0 20px 0 20px;
			margin-right: 20px;
			border-right: 1px solid $onSurface;
		}
	}

	.statistics__courses {
		padding: 10px;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
	}
}

