%lifted-surface {
	box-shadow: 0px 0px 25px 5px rgba(0,0,0,0.30);
	border-radius: 30px;
}

%action-button {
	/* Set the font and background color */
	background-color: $surface;
	color: $secondary;

	/* Set the round border settings */
	border: 2px solid $secondary;
	border-radius: 30px;

	/* Font size */
	font-size: 15px;

	/* Animation transition */
	transition: 0.2s;

	/* Define what happens on hover */
	&:hover {
		background-color: $primary;
		color: $onPrimary;
		border-color: $surface;
  }
}

%danger-button {
	@extend %action-button;

	/* Set the font and background color */
	background-color: $surface;
	color: $error;
	border: 2px solid $error;

	/* Define what happens on hover */
	&:hover {
		background-color: $error;
		color: $onError;
  }
}

%input {
  border: none;
  margin-bottom: 1vh;
  background-color: $surface;
  color: $onSurface;
  padding: 0.7rem;
  &:focus {
    outline-color: $secondary;
    border: none;
  }
}

%form-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 700px;

  button {
    @extend %action-button;
    width: 10vw;
    min-width: 200px;
    padding: 2vh;
    border-radius: 4vh;
  }
  button.delete {
		@extend %danger-button;
  }

  .hidden{
    display: none;
  }

  form{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    * {
      margin-bottom: 1vh;
      margin-top: 1vh;
    }
    label{
      display: flex;
      flex-direction: column;
      align-items: center;
      color: $onBackground;
      width: 100%;
      input, textarea{
        @extend %input;
        width: 70%;
      }
    }
  }
}

%horizontal-nav-bar {
  display: flex;
  flex-direction: column;
  margin-bottom: 2vh;
  ul{
    display: flex;
    align-self: center;
    flex-direction: row;
    list-style-type: none;
    li.active {
      color: $secondary;
      border-color: $secondary;
      a {
        color: $secondary;
      }
    }
    li{
      border-bottom: 1px solid white;
      padding: 2rem;
      margin: 0 2rem 0 2rem;
      transition: 0.2s;
      &:hover{
        border-color: $secondary;
        color: $onBackground;
      }
      a{
        color: $onBackground;
        text-decoration: none;
      }
    }
  }
}

%table {
  table{
    width: 100%;
    border-collapse: collapse;
    th, td{
      padding: 20px;
    }
		th { color: $onBackgroundFaded; }
		td {
      border-bottom: 1px solid $onBackgroundFaded;
		}
    td{
      button, a{
				@extend %action-button;
        padding: 8px 14px;
        text-decoration: none;
        &:hover{
          background-color: $secondary;
          color: $onSecondary;
        }
      }
    }
  }
}

%popup {
	position: fixed;
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0, 0.8);
	top: 0;
	left: 0;
}
