/* ============= The register styling ============= */
.register {
	display: flex;
	flex-direction: column;
	align-items: center;

	.header {
		width: 50%;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		button {
			@extend %action-button;
			border-radius: 20px;
			margin: 1rem;
		}
	}

	.student-row {
		height: 30px;

		display: flex;
		flex-direction: row;
		align-items: center;

		margin: 0;
		padding: 0;

		border: 1px solid $surface;
		p {
			width: 150px;
			height: 100%;
			font-weight: bold;
		}
		.element {
			height: 100%;
			display: flex;
			flex-direction: row;
			align-items: center;
			p {
				height: 100%;
				width: 30px;
				text-align: center;
				border: 1px solid $primary;
				&:hover{
					background-color: $primary;
					color: $onPrimary
				}
			}
		}
	}

	.popup {
		position: fixed;
		width: 100%;
		height: 100%;
		background-color: rgba(0,0,0, 0.8);
		top: 0;
		left: 0;
		@extend %form-container;
		form {
			background-color: $background;
			padding: 30px;
			border-radius: 10px;
			margin: auto;
			width: 50%;
		}
	}
}

