/* ========= General Course Files ============= */
.file-list {
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	.file-form {
		@extend %form-container;
		input[type="submit"] {
			@extend %action-button;
			padding: 10px;
		}
	}

	ul{
		min-width: 80%;
		display: flex;
		flex-direction: column;
		align-items: center;
		list-style-type: none;

		li{
			@extend %lifted-surface;
			margin: 10px;
			width: 100%;
			max-width: 700px;

			display: flex;
			flex-direction: row;
			justify-content: space-between;
			align-items: center;

			padding: 20px;
			background-color: $surface;
			color: $onSurface;
			.filename {
				color: $onBackground;
			}
			i {
				cursor: pointer;
				&:hover {color: $primary;}
			}
			i.delete:hover { color: $error; }

			.actions {
				display: flex;
				flex-direction: row;
				justify-content: center;
				align-items: center;
				i { margin-left: 15px;}
			}

			p {
				margin: 0;
			}
		}
	}
}
